
.formControl.error
  .textarea
    border: 2px solid red !important
.someError
  color: #4F2F00
.commonError
  color: red
.textarea
  border: 2px solid #4F2F00
  height: 26px
  border-radius: 50px
  padding: 10px
  font-size: 18px
  background-color: white
  &:focus-visible
    outline: 0

@media screen and (max-width : 450px )
  .textarea
    height: 15px
    border-radius: 50px
    padding: 10px
    font-size: 14px
    background-color: white
